<template>
  <div class="linked">
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <section class="van-doc-demo-section">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

        <van-cell-group>
          <van-cell title="交易和咨询业务" />

          <van-row v-for="item in list">
            <van-col span="4">颜欢</van-col>
            <van-col span="8">021-38032486</van-col>
            <van-col span="12">yanhuan019043@gtjas.com1234</van-col>
          </van-row>
        </van-cell-group>

        <van-cell-group>
          <van-cell title="交易和咨询业务" />

          <van-row v-for="item in list">
            <van-col span="4">颜欢</van-col>
            <van-col span="8">021-38032486</van-col>
            <van-col span="12">yanhuan019043@gtjas.com1234</van-col>
          </van-row>
        </van-cell-group>

        <van-cell-group>
          <van-cell title="交易和咨询业务" />

          <van-row v-for="item in list">
            <van-col span="4">颜欢</van-col>
            <van-col span="8">021-38032486</van-col>
            <van-col span="12">yanhuan019043@gtjas.com1234</van-col>
          </van-row>
        </van-cell-group>

      </van-pull-refresh>
    </section>
  </div>
</template>

<script>
  export default {
    name: "linked",
    data(){
      return {
        title: '业务人员联系方式',
        isLoading: false,
        list: [1, 2, 3]
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.count++;
        }, 500);
      }
    }
  }
</script>

<style scoped>

  .linked .van-pull-refresh__track {
    min-height: calc(100vh - 60px);
  }
  .linked .van-cell-group {
    padding: 10px 15px 0;
    margin-top: 10px;
    background-color: #ffffff;
  }
  .linked .van-cell {
    padding: 0;
    font-size: 15px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(32, 32, 32, 1);
    line-height: 21px;
  }
  .linked .van-cell:not(:last-child)::after {
    display: none;
  }
  .linked .van-row {
    font-size: 13px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(155, 155, 155, 1);
    line-height: 18.5px;
    padding: 10px 0;
    border-bottom: 1px solid rgba(240,240,240,1);
  }
  .linked .van-row .van-col {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .linked .van-row:last-child {
    border: none;
  }
</style>



